.container {
  /* LAYOUT */
  margin: 50px;
  padding: 30px 0 10px 0;
  width: 85vw;
  height: -webkit-fit-content;
  height: -moz-fit-content;
  height: fit-content;
  background: rgba(253, 253, 253, 0.664);
  border-radius: 5px;
  position: relative;
  box-shadow: 2px 2px 4px goldenrod;
  color: rgb(1, 19, 7);
  /* GRID template*/
  display: grid;
  grid-template-columns: 6fr 1fr 1fr 4fr;
  grid-template-rows: 40px 40px 40px auto;
  grid-column-gap: 20px;
  -webkit-column-gap: 20px;
          column-gap: 20px;
  grid-row-gap: 5px;
  row-gap: 5px;
  justify-items: center;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-content: space-evenly;
          align-content: space-evenly;
}

.title {
  grid-area: 1 / 1 / span 3 / span 1;
  color: rgb(4, 68, 43);
  font-size: 40px;
  line-height: normal;
  align-self: center;
  text-align: center;
  margin: 0 6vw 0 2vw;
  text-shadow: 1px 1px 1px honeydew;
}

.container span {
  border-radius: 5px;
  font-size: 14px;
  padding: 6px 8px;
  font-weight: 500;
  -webkit-align-self: center;
          align-self: center;
  box-shadow: 2px 2px 2px black;
}

.scores:nth-child(2) {
  grid-area: 2 / 2 / span 1 / span 2;
  background-color: goldenrod;
}

.scores:nth-child(3) {
  grid-area: 3 / 2 / span 1 / span 2;
  background-color: salmon;
}

#recipeComponent img {
  grid-area: 2 / 4 / span 4 / span 1;
  justify-self: center;
  max-width: 400px;
  min-width: 300px;
  border-radius: 20px;
  box-shadow: 4px 4px 2px gray;
}

#recipeComponent img:hover {
  -webkit-filter: brightness(150%) contrast(150%);
          filter: brightness(150%) contrast(150%);
